@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  min-height: 100vh;
  color: #FFFFFF;
  background-color: #0D2436;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.app-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80vw;
  margin-top: 5vh;
  margin-bottom: 5vh;
}

.movies-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  grid-gap: 15px 20px;
  gap: 15px 20px;
  flex-wrap: wrap;
  width: 100%;
}


/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1080px) {
  .movies-row {
    justify-content: center;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.submit-button {
    text-align: center;
    padding: 10px 40px 10px 40px;
    border-radius: 18px;
    background-color: #009B86;
    color: #FFFFFF;
    font-size: 0.6em;
    font-weight: bold;
    margin-top: 8vh;
    margin-bottom: 5vh;
    transition: 0.2s;
    cursor: pointer;
}

.submit-button:hover {
    background-color: #34AC9C;
    transition: 0.2s ease;
    color: #CCCCCC;
    
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1020px) {
    .submit-button {
        font-size: 0.6em;
    }   
}

@media screen and (max-width: 500px) {
    .submit-button {
        font-size: 0.8em;
    }   
}
.banner {
    width: 100%;
    margin-top: 4vh;
    margin-bottom: 2vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-size: 1em;
    font-weight: bold;
    text-align: left;
}


/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1080px) {
    .banner {
        font-size: 1.2em;
        text-align: center;
    }
}

@media screen and (max-width: 500px) {
    .banner {
        font-size: 1.5em;
        text-align: center;
    }
}
/* Card styles depending on status */
.card-unselected {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 360px;
    width: 240px;
    padding: 1vh;
    background-color: #0D2436;
    border: 1.5px solid #009B86;
    border-radius: 15px;
    transition: 0.2s;
}

.card-selected {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 360px;
    width: 240px;
    padding: 1vh;
    background-color: #009B86;
    border: 1.5px solid #009B86;
    border-radius: 15px;
    transition: 0.2s;
}


/* HOVER */
.card-unselected:hover, .card-selected:hover {
    background-color: #34AC9C;
    transition: 0.2s;
}

.card-unselected:hover .card-button, .card-selected:hover .card-selected-button {
    background-color: #34AC9C;
    transition: 0.2s;
    border: 1px solid #FFFFFF;
}

.card-button:hover, .card-selected-button:hover {
    color: #CCCCCC;
    transition: 0.2s ease;
}


/* Cards elements */
.card-image {
    height: 180px;
    object-fit: cover;
    margin: 0;
    box-shadow: 3px 3px 8px -2px black;
}

.card-title {
    font-size: 0.550em;
    font-weight: 700;
    margin: 0;
}

.card-button {
    font-size: 0.475em;
    font-weight: 500;
    background-color: #009B86;
    border: 1px solid #009B86;
    padding: 8px 30px 8px 30px;
    border-radius: 18px;
    transition: 0.2s ease;
    cursor: pointer;
}

.card-selected-button {
    font-size: 0.475em;
    font-weight: 500;
    background-color: #009B86;
    border: 1px solid #009B86;
    padding: 8px 30px 8px 30px;
    border-radius: 18px;
    transition: 0.2s ease;
    cursor: pointer;
    transition: 0.2s;
    border: 1px solid #FFFFFF;
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1020px) {
    .card-title {
        font-size: 0.7em;
    }

    .card-button {
        font-size: 0.6em;
    }

    .card-selected-button {
        font-size: 0.6em;
    }
}

@media screen and (max-width: 500px) {
    .card-title {
        font-size: 0.9em;
    }

    .card-button {
        font-size: 0.8em;
    }

    .card-selected-button {
        font-size: 0.8em;
    }
}
.modal-background-box {
    position: fixed;
    background: #00000050;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    margin: 0 auto;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    background: #FFFFFF;
    border-radius: 15px;
    padding: 3vh;
    overflow: auto;
    box-shadow: 1px 1px 8px 1px black;
}

.modal-title {
    color: black;
    margin-bottom: 3vh;
}

.modal-text {
    color: black;
    font-size: 0.5em;
}

.modal-image {
    width: 50%;
    margin-top: 15px;
    margin-bottom: 15px;
}

.modal-close-button {
    color: black;
    font-size: 0.475em;
    font-weight: 500;
    border: 1px solid black;
    margin-top: 12px;
    padding: 8px 30px 8px 30px;
    border-radius: 18px;
    transition: 0.2s ease;
    cursor: pointer;
}

.modal-close-button:hover {
    color: #CCCCCC;
    border: 1px solid #CCCCCC;
    transition: 0.2s ease;
}

/* TAILLE D'ECRAN : ajustements */
@media screen and (max-width: 1020px) {
    .modal-box {
        width:60%;
    }
    .modal-title {
        font-size: 0.8em;
    }
    .modal-text {
        font-size: 0.6em;
    }
    .modal-close-button {
        font-size: 0.6em;
    }   
}

@media screen and (max-width: 500px) {
    .modal-box {
        width:70%;
    }
    .modal-title {
        font-size: 1em;
    }
    .modal-text {
        font-size: 0.8em;
    }
    .modal-close-button {
        font-size: 0.7em;
    }   
}
